/* eslint-disable react/no-danger-with-children */
import styled from 'styled-components';
import Tooltip from '../Tooltip/Tooltip';
import { MD5 } from '../../utils/utils';
import classNames from 'classnames';


const Wrapper = styled.div`
  .text {
    border-bottom : 1px dashed #93A1B8;
    width : max-content;
    cursor : pointer;
  }
`

export default function UnderlineText({text,id = MD5(text) ,tip,multiline,className}){
  const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
  return(
    <Wrapper className={className}>
      <div className='text' data-for={id} data-tip={tip} data-event={isMobile ? 'click' : undefined}>
        {text}
      </div>
      {tip &&<Tooltip id={id} width='256' padding='12px' place='bottom' borderRadius={8} multiline={multiline}>
      </Tooltip>}
    </Wrapper>
  )
}